<template>
  <div class="prompt-modal-container" v-if="visible" :class="{ 'fade-in': fadeIn }">
    <div class="overlay" @click.self="closeIfClickable"></div>
    <div class="prompt-modal">
      <div class="prompt-header">
        <h2>温馨提示</h2>
      </div>
      <div class="prompt-content">
        <div class="setting-item">
          <span class="setting-label">是否全屏显示</span>
          <div class="toggle-switch" @click="toggleFullscreen" :class="{ active: isFullscreen }">
            <div class="toggle-handle"></div>
          </div>
        </div>
        <div class="setting-item">
          <span class="setting-label">是否开启音乐</span>
          <div class="toggle-switch" @click="toggleMusic" :class="{ active: musicEnabled }">
            <div class="toggle-handle"></div>
          </div>
        </div>
      </div>
      <div class="prompt-footer">
        <button class="confirm-button" @click="confirm">确认</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PromptModal',
  props: {
    visible: {
      type: Boolean,
      default: true
    },
    closable: {
      type: Boolean,
      default: true
    }
  },
  emits: ['update:visible', 'confirm', 'toggle-music'],
  data() {
    return {
      isFullscreen: false,
      musicEnabled: false,
      fadeIn: false
    }
  },
  watch: {
    visible(newVal) {
      if (newVal) {
        // 当显示时，延迟添加渐显类，创建动画效果
        setTimeout(() => {
          this.fadeIn = true;
        }, 50);
      } else {
        this.fadeIn = false;
      }
    }
  },
  mounted() {
    // 添加全屏变化事件监听
    document.addEventListener('fullscreenchange', this.handleFullscreenChange);
    document.addEventListener('mozfullscreenchange', this.handleFullscreenChange);
    document.addEventListener('webkitfullscreenchange', this.handleFullscreenChange);
    document.addEventListener('msfullscreenchange', this.handleFullscreenChange);
    
    // 初始化渐显效果
    setTimeout(() => {
      this.fadeIn = true;
    }, 50);
    
    // 添加页面刷新事件监听
    window.addEventListener('beforeunload', this.handleBeforeUnload);
  },
  beforeUnmount() {
    // 移除事件监听
    document.removeEventListener('fullscreenchange', this.handleFullscreenChange);
    document.removeEventListener('mozfullscreenchange', this.handleFullscreenChange);
    document.removeEventListener('webkitfullscreenchange', this.handleFullscreenChange);
    document.removeEventListener('msfullscreenchange', this.handleFullscreenChange);
    window.removeEventListener('beforeunload', this.handleBeforeUnload);
  },
  methods: {
    toggleFullscreen() {
      this.isFullscreen = !this.isFullscreen;
      if (this.isFullscreen) {
        this.enterFullscreen();
      } else {
        this.exitFullscreen();
      }
    },
    toggleMusic() {
      this.musicEnabled = !this.musicEnabled;
      // 音乐逻辑将在后续实现
      this.$emit('toggle-music', this.musicEnabled);
    },
    confirm() {
      // 先淡出，然后再关闭
      this.fadeIn = false;
      setTimeout(() => {
        this.$emit('confirm', {
          fullscreen: this.isFullscreen,
          music: this.musicEnabled
        });
        this.$emit('update:visible', false);
      }, 300); // 等待淡出动画完成
    },
    closeIfClickable() {
      if (this.closable) {
        // 先淡出，然后再关闭
        this.fadeIn = false;
        setTimeout(() => {
          this.$emit('update:visible', false);
        }, 300); // 等待淡出动画完成
      }
    },
    // 进入全屏
    enterFullscreen() {
      const docElm = document.documentElement;
      if (docElm.requestFullscreen) {
        docElm.requestFullscreen();
      } else if (docElm.mozRequestFullScreen) { // Firefox
        docElm.mozRequestFullScreen();
      } else if (docElm.webkitRequestFullscreen) { // Chrome, Safari
        docElm.webkitRequestFullscreen();
      } else if (docElm.msRequestFullscreen) { // IE11
        docElm.msRequestFullscreen();
      }
    },
    // 退出全屏
    exitFullscreen() {
      if (document.exitFullscreen) {
        document.exitFullscreen();
      } else if (document.mozCancelFullScreen) { // Firefox
        document.mozCancelFullScreen();
      } else if (document.webkitExitFullscreen) { // Chrome, Safari
        document.webkitExitFullscreen();
      } else if (document.msExitFullscreen) { // IE11
        document.msExitFullscreen();
      }
    },
    // 检查当前是否全屏
    checkFullscreen() {
      return !!(document.fullscreenElement || document.mozFullScreenElement || 
                document.webkitFullscreenElement || document.msFullscreenElement);
    },
    // 监听全屏变化
    handleFullscreenChange() {
      this.isFullscreen = this.checkFullscreen();
    },
    // 处理页面刷新
    handleBeforeUnload() {
      // 页面刷新时保存设置到localStorage
      localStorage.setItem('appSettings', JSON.stringify({
        fullscreen: this.isFullscreen,
        music: this.musicEnabled
      }));
    }
  }
}
</script>

