    min-width: clamp(120px, 40vw, 180px);
  }
  
  .setting-item {
    padding: clamp(5px, 2vw, 8px) 0;
  }
}

/* 针对不同刷新手势的处理 */
@supports (-webkit-overflow-scrolling: touch) {
  /* iOS设备 - 支持弹性滚动 */
