    -webkit-overflow-scrolling: touch;
    overscroll-behavior-y: contain;
  }
}

@supports (overscroll-behavior-y: contain) {
  /* 现代浏览器 - 支持overscroll-behavior */
  html, body {
    overscroll-behavior-y: contain;
  }
}
