<template>
  <div class="home-page">
    <div class="content" ref="contentBox">
      <div class="text-container">
        <div class="sliding-text" ref="slidingText">
          <img src="../assets/images/文字.svg" alt="ORTHYMOS" class="text-svg">
        </div>
        <div class="horizontal-line" ref="horizontalLine">
          <img src="../assets/images/横线.svg" alt="line" class="line-svg">
        </div>
        <!-- 添加旋转球体 -->
        <div class="globe-container" ref="globeContainer" 
             @mousedown="startRotate" 
             @mousemove="rotateGlobe" 
             @mouseup="endRotate" 
             @mouseleave="endRotate"
             @touchstart="startTouchRotate"
             @touchmove="touchRotateGlobe"
             @touchend="endTouchRotate"
             @touchcancel="endTouchRotate"
             @dragstart.prevent
             @selectstart.prevent>
          <img src="../assets/images/球.svg" alt="globe" class="globe-svg" ref="globeSvg" draggable="false">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { eventBus } from '../App.vue';

export default {
  name: 'HomePage',
  data() {
    return {
      animationStarted: false,
      textAnimationComplete: false,
      waitForConfirmation: true, // 添加等待确认的标志
      // 添加球体旋转相关数据
      isRotating: false,
      lastMouseX: 0,
      lastTouchX: 0, // 新增：记录上次触摸位置
      rotationSpeed: 0,
      currentRotation: 0,
      inertiaInterval: null,
      inertiaDecay: 0.98, // 提高衰减系数，使衰减更慢（接近1表示几乎不衰减）
      baseRotationSpeed: 18, // 基础旋转速度，秒/圈
      sensitivityFactor: 0.8, // 降低灵敏度，数值越小灵敏度越低
      touchSensitivityFactor: 1.2, // 触摸灵敏度系数，比鼠标稍高一些
      maxRotationSpeed: 12, // 最大旋转速度限制
      // 页面切换相关
      transitionThreshold: 10, // 降低触发页面切换的速度阈值
      isTransitioning: false,
      transitionStarted: false,
      transitionDuration: 1800 // 缩短切换动画时间（毫秒）
    }
  },
  mounted() {
    // 提前加载SVG图片
    this.preloadSvg();
    
    // 页面加载后的初始化逻辑
    this.initPage();
    window.addEventListener('resize', this.handleResize);
    
    // 全局阻止拖拽行为
    document.addEventListener('dragstart', this.preventDrag);
    
    // 监听事件总线中的开始动画事件
    eventBus.on('startHomeAnimation', this.handleStartAnimation);
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.handleResize);
    document.removeEventListener('dragstart', this.preventDrag);
    // 清除惯性计算的定时器
    if (this.inertiaInterval) {
      clearInterval(this.inertiaInterval);
    }
    // 移除事件监听
    eventBus.off('startHomeAnimation', this.handleStartAnimation);
  },
  methods: {
    // 处理开始动画事件
    handleStartAnimation() {
      this.waitForConfirmation = false;
      // 添加短暂延迟后开始动画
      setTimeout(() => {
        this.startAnimation();
      }, 300);
    },
    
    preventDrag(e) {
      // 全局阻止拖拽
      e.preventDefault();
    },
    preloadSvg() {
      const img1 = new Image();
      img1.src = require('../assets/images/文字.svg');
      const img2 = new Image();
      img2.src = require('../assets/images/横线.svg');
      const img3 = new Image();
      img3.src = require('../assets/images/球.svg');
    },
    initPage() {
      // 初始化页面
      this.handleResize();
    },
    handleResize() {
      // 自动应用CSS媒体查询的样式，无需额外JS调整
    },
    startAnimation() {
      if (this.animationStarted || this.waitForConfirmation) return;
      
      this.animationStarted = true;
      const textElement = this.$refs.slidingText;
      const contentBox = this.$refs.contentBox;
      const lineElement = this.$refs.horizontalLine;
      const globeElement = this.$refs.globeContainer;
      
      // 先显示边框
      requestAnimationFrame(() => {
        contentBox.classList.add('show');
        
        // 边框显示后再显示文字
        setTimeout(() => {
          textElement.classList.add('animate');
          
          // 文字显示后再显示横线
          setTimeout(() => {
            lineElement.classList.add('animate');
            
            // 横线显示后再显示旋转球体
            setTimeout(() => {
              globeElement.classList.add('animate');
            }, 800);
          }, 800);
        }, 600);
      });
      
      // 动画结束后标记完成
      setTimeout(() => {
        this.textAnimationComplete = true;
      }, 3200); // 考虑了所有动画的总时间
    },
    // 开始旋转球体
    startRotate(event) {
      if (!this.textAnimationComplete || this.isTransitioning) return;
      
      // 阻止默认行为和文本选择
      event.preventDefault();
      
      // 禁用页面文本选择
      document.body.style.userSelect = 'none';
      document.body.style.webkitUserSelect = 'none';
      document.body.style.mozUserSelect = 'none';
      document.body.style.msUserSelect = 'none';
      
      this.isRotating = true;
      this.lastMouseX = event.clientX;
      
      // 停止当前的惯性效果
      if (this.inertiaInterval) {
        clearInterval(this.inertiaInterval);
        this.inertiaInterval = null;
      }
      
      // 停止CSS动画
      const globeSvg = this.$refs.globeSvg;
      if (globeSvg) {
        // 获取当前旋转角度
        const computedStyle = window.getComputedStyle(globeSvg);
        
        // 使用浏览器兼容的方式获取当前变换矩阵
        let matrix;
        try {
          // 尝试使用标准DOMMatrix
          if (window.DOMMatrix) {
            matrix = new DOMMatrix(computedStyle.transform);
          } 
          // 尝试使用WebKit前缀版本
          else if (window.WebKitCSSMatrix) {
            /* eslint-disable no-undef */
            matrix = new WebKitCSSMatrix(computedStyle.transform);
            /* eslint-enable no-undef */
          } 
          // 如果都不支持，使用简单的回退方法
          else {
            // 简单的回退，不从矩阵中提取角度
            this.currentRotation = 0;
            globeSvg.style.animation = 'none';
            globeSvg.style.transform = `rotate(${this.currentRotation}deg)`;
            return;
          }
          
          // 从矩阵中提取旋转角度
          this.currentRotation = Math.atan2(matrix.b, matrix.a) * (180 / Math.PI);
        } catch (e) {
          // 如果出错，使用简单的回退方法
          console.warn('无法获取当前旋转角度:', e);
          this.currentRotation = 0;
        }
        
        // 移除CSS动画
        globeSvg.style.animation = 'none';
        // 设置当前旋转位置
        globeSvg.style.transform = `rotate(${this.currentRotation}deg)`;
      }
    },
    // 旋转球体
    rotateGlobe(event) {
      if (!this.isRotating || this.isTransitioning) return;
      
      // 阻止默认行为
      event.preventDefault();
      
      const deltaX = event.clientX - this.lastMouseX;
      this.lastMouseX = event.clientX;
      
      // 只允许顺时针旋转（向右拖动）
      if (deltaX > 0) {
        // 计算旋转速度，应用灵敏度系数降低灵敏度
        this.rotationSpeed = Math.min(deltaX * this.sensitivityFactor, this.maxRotationSpeed);
        
        // 检查是否达到切换页面的阈值
        if (this.rotationSpeed > this.transitionThreshold && !this.transitionStarted) {
          this.startPageTransition();
          return;
        }
        
        // 更新旋转角度（顺时针方向）
        this.currentRotation += this.rotationSpeed;
        
        // 应用旋转
        const globeSvg = this.$refs.globeSvg;
        if (globeSvg) {
          globeSvg.style.transform = `rotate(${this.currentRotation}deg)`;
        }
      }
    },
    // 结束旋转，开始惯性效果
    endRotate() {
      if (!this.isRotating || this.isTransitioning) return;
      
      this.isRotating = false;
      
      // 恢复页面文本选择
      document.body.style.userSelect = '';
      document.body.style.webkitUserSelect = '';
      document.body.style.mozUserSelect = '';
      document.body.style.msUserSelect = '';
      
      // 检查是否达到切换页面的阈值
      if (this.rotationSpeed > this.transitionThreshold && !this.transitionStarted) {
        this.startPageTransition();
        return;
      }
      
      // 如果有旋转速度，启动惯性效果
      if (Math.abs(this.rotationSpeed) > 0.1) {
        this.startInertia();
      } else {
        // 速度太小，恢复原始动画
        this.resetOriginalAnimation();
      }
    },
    // 启动惯性效果
    startInertia() {
      // 清除之前的惯性效果
      if (this.inertiaInterval) {
        clearInterval(this.inertiaInterval);
      }
      
      // 设置惯性效果的定时器
      this.inertiaInterval = setInterval(() => {
        // 应用惯性衰减
        this.rotationSpeed *= this.inertiaDecay;
        
        // 检查是否达到切换页面的阈值
        if (this.rotationSpeed > this.transitionThreshold && !this.transitionStarted) {
          clearInterval(this.inertiaInterval);
          this.inertiaInterval = null;
          this.startPageTransition();
          return;
        }
        
        // 更新旋转角度（保持顺时针方向）
        if (this.rotationSpeed > 0) {
          this.currentRotation += this.rotationSpeed;
          
          // 应用旋转
          const globeSvg = this.$refs.globeSvg;
          if (globeSvg) {
            globeSvg.style.transform = `rotate(${this.currentRotation}deg)`;
          }
        }
        
        // 当速度足够小时，恢复原始动画
        if (Math.abs(this.rotationSpeed) < 0.1) {
          clearInterval(this.inertiaInterval);
          this.inertiaInterval = null;
          this.resetOriginalAnimation();
        }
      }, 16); // 约60fps
    },
    // 恢复原始动画
    resetOriginalAnimation() {
      const globeSvg = this.$refs.globeSvg;
      if (globeSvg) {
        // 计算当前角度对应的0-360度范围内的值
        const normalizedRotation = ((this.currentRotation % 360) + 360) % 360;
        
        // 创建自定义的keyframes动画
        const keyframes = `
          @keyframes resumeRotate {
            0% { transform: rotate(${normalizedRotation}deg); }
            100% { transform: rotate(${normalizedRotation + 360}deg); }
          }
        `;
        
        // 添加样式到文档
        const styleSheet = document.createElement('style');
        styleSheet.type = 'text/css';
        styleSheet.innerText = keyframes;
        document.head.appendChild(styleSheet);
        
        // 应用新的动画，从当前角度继续旋转
        globeSvg.style.animation = `resumeRotate ${this.baseRotationSpeed}s linear infinite`;
      }
    },
    // 开始页面切换动画
    startPageTransition() {
      if (this.transitionStarted) return;
      
      this.transitionStarted = true;
      this.isTransitioning = true;
      
      // 清除惯性计算的定时器
      if (this.inertiaInterval) {
        clearInterval(this.inertiaInterval);
        this.inertiaInterval = null;
      }
      
      // 获取元素
      const textElement = this.$refs.slidingText;
      const lineElement = this.$refs.horizontalLine;
      const globeElement = this.$refs.globeContainer;
      const contentBox = this.$refs.contentBox;
      const globeSvg = this.$refs.globeSvg;
      
      // 恢复球体的自转动画
      if (globeSvg) {
        // 计算当前角度
        const normalizedRotation = ((this.currentRotation % 360) + 360) % 360;
        
        // 创建自定义的keyframes动画
        const keyframes = `
          @keyframes transitionRotate {
            0% { transform: rotate(${normalizedRotation}deg); }
            100% { transform: rotate(${normalizedRotation + 720}deg); }
          }
        `;
        
        // 添加样式到文档
        const styleSheet = document.createElement('style');
        styleSheet.type = 'text/css';
        styleSheet.innerText = keyframes;
        document.head.appendChild(styleSheet);
        
        // 应用新的动画，从当前角度继续旋转，但速度更快（2圈）
        globeSvg.style.animation = `transitionRotate ${this.transitionDuration / 1000}s linear forwards`;
      }
      
      // 添加页面裂开的类
      contentBox.classList.add('splitting');
      textElement.classList.add('split-up');
      lineElement.classList.add('split-up');
      globeElement.classList.add('split-down');
      
      // 通知根实例切换页面
      setTimeout(() => {
        // 使用根实例的方法而不是父组件
        this.$root.switchPage('MainPage');
      }, this.transitionDuration); // 使用设定的动画时间
    },
    
    // 触摸开始事件处理
    startTouchRotate(event) {
      if (!this.textAnimationComplete || this.isTransitioning) return;
      
      // 阻止默认行为和滚动
      event.preventDefault();
      
      this.isRotating = true;
      // 获取第一个触摸点的X坐标
      this.lastTouchX = event.touches[0].clientX;
      
      // 停止当前的惯性效果
      if (this.inertiaInterval) {
        clearInterval(this.inertiaInterval);
        this.inertiaInterval = null;
      }
      
      // 停止CSS动画，与鼠标事件处理相同的逻辑
      const globeSvg = this.$refs.globeSvg;
      if (globeSvg) {
        // 获取当前旋转角度
        const computedStyle = window.getComputedStyle(globeSvg);
        
        // 使用浏览器兼容的方式获取当前变换矩阵
        let matrix;
        try {
          // 尝试使用标准DOMMatrix
          if (window.DOMMatrix) {
            matrix = new DOMMatrix(computedStyle.transform);
          } 
          // 尝试使用WebKit前缀版本
          else if (window.WebKitCSSMatrix) {
            /* eslint-disable no-undef */
            matrix = new WebKitCSSMatrix(computedStyle.transform);
            /* eslint-enable no-undef */
          } 
          // 如果都不支持，使用简单的回退方法
          else {
            // 简单的回退，不从矩阵中提取角度
            this.currentRotation = 0;
            globeSvg.style.animation = 'none';
            globeSvg.style.transform = `rotate(${this.currentRotation}deg)`;
            return;
          }
          
          // 从矩阵中提取旋转角度
          this.currentRotation = Math.atan2(matrix.b, matrix.a) * (180 / Math.PI);
        } catch (e) {
          // 如果出错，使用简单的回退方法
          console.warn('无法获取当前旋转角度:', e);
          this.currentRotation = 0;
        }
        
        // 移除CSS动画
        globeSvg.style.animation = 'none';
        // 设置当前旋转位置
        globeSvg.style.transform = `rotate(${this.currentRotation}deg)`;
      }
    },
    
    // 触摸移动事件处理
    touchRotateGlobe(event) {
      if (!this.isRotating || this.isTransitioning) return;
      
      // 阻止默认行为和滚动
      event.preventDefault();
      
      // 获取当前触摸位置
      const currentTouchX = event.touches[0].clientX;
      const deltaX = currentTouchX - this.lastTouchX;
      this.lastTouchX = currentTouchX;
      
      // 只允许顺时针旋转（向右拖动）
      if (deltaX > 0) {
        // 计算旋转速度，应用触摸灵敏度系数
        this.rotationSpeed = Math.min(deltaX * this.touchSensitivityFactor, this.maxRotationSpeed);
        
        // 检查是否达到切换页面的阈值
        if (this.rotationSpeed > this.transitionThreshold && !this.transitionStarted) {
          this.startPageTransition();
          return;
        }
        
        // 更新旋转角度（顺时针方向）
        this.currentRotation += this.rotationSpeed;
        
        // 应用旋转
        const globeSvg = this.$refs.globeSvg;
        if (globeSvg) {
          globeSvg.style.transform = `rotate(${this.currentRotation}deg)`;
        }
      }
    },
    
    // 触摸结束事件处理
    endTouchRotate() {
      if (!this.isRotating || this.isTransitioning) return;
      
      this.isRotating = false;
      
      // 检查是否达到切换页面的阈值
      if (this.rotationSpeed > this.transitionThreshold && !this.transitionStarted) {
        this.startPageTransition();
        return;
      }
      
      // 如果有旋转速度，启动惯性效果
      if (Math.abs(this.rotationSpeed) > 0.1) {
        this.startInertia();
      } else {
        // 速度太小，恢复原始动画
        this.resetOriginalAnimation();
      }
    }
  }
}
</script>

<style scoped>
