  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  backface-visibility: hidden;
}

.sliding-text {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: -200%;
  left: 0;
  right: 0;
  margin: 0 auto;
  opacity: 0;
