}

/* 页面裂开时的上滑效果 */
.sliding-text.split-up {
  animation: splitUp 1.8s ease-in-out forwards;
}

.horizontal-line.split-up {
  animation: splitLineRight 1.8s ease-in-out forwards;
}

@keyframes splitUp {
  0% {
    transform: translateY(-50%);
    opacity: 1;
  }
  100% {
    transform: translateY(-150%);
    opacity: 0;
  }
}

@keyframes splitLineRight {
  0% {
    transform: translateX(0);
    opacity: 1;
  }
  100% {
    transform: translateX(100%);
    opacity: 0;
  }
}

/* 旋转球体样式 */
.globe-container {
