  will-change: opacity;
  z-index: 15; /* 修改此处，将z-index值设置为大于横线的值 */
  cursor: grab; /* 添加抓取光标 */
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  touch-action: pan-y; /* 允许垂直滚动，但水平手势会被捕获 */
}

.globe-container.animate {
  opacity: 1;
}

.globe-container:active {
  cursor: grabbing; /* 抓取时的光标 */
}

/* 页面裂开时的下滑效果 */
.globe-container.split-down {
  animation: splitDown 1.8s ease-in-out forwards;
}

@keyframes splitDown {
  0% {
    transform: translateY(0);
    opacity: 1;
  }
  100% {
    transform: translateY(150%);
    opacity: 0;
  }
}

/* 球体自转动画与下滑动画分离，让球体图像负责旋转，容器负责位移 */
.globe-svg {
  width: auto;
  height: 50vh; /* 减小球体高度，从55vh改为50vh */
  max-height: 50%; /* 减小最大高度限制，从55%改为50% */
  animation: rotate 20s linear infinite;
  transform-origin: center center;
  transition: height 0.3s ease-out, max-height 0.3s ease-out; /* 添加过渡效果 */
  will-change: transform;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-drag: none;
  pointer-events: none;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg); /* 顺时针旋转 */
  }
}

