<template>
  <div class="chapter3-page">
    <div class="content">
      <!-- 这里是章节3的内容区域，目前只有底色和边框 -->
      <div class="book-cover-container" 
           :class="{ animate: animateBookCover }" 
           :style="{ transform: `translateX(${bookCoverPosition}px)` }"
           @mousedown="startDrag"
           @mousemove="dragBookCover"
           @mouseup="endDrag"
           @mouseleave="endDrag"
           @touchstart="startTouchDrag"
           @touchmove="touchDragBookCover"
           @touchend="endTouchDrag"
           @touchcancel="endTouchDrag"
           ref="bookCoverContainer">
        <img src="@/assets/images/书封.svg" alt="第三幕" class="book-cover-svg" draggable="false">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Chapter3Page',
  data() {
    return {
      animateBookCover: false,
      isDragging: false,
      startX: 0,
      startTouchX: 0,
      bookCoverPosition: -9999, // 初始位置在屏幕外，让CSS的transform先生效
      originalPosition: 0,
      maxLeftSlide: -50, // 向左最多可以拖动的像素数
      transitionThreshold: 0.4, // 右滑到视口宽度40%时触发跳转
      isTransitioning: false,
      windowWidth: window.innerWidth
    };
  },
  mounted() {
    // 页面加载后立即启动动画，减少初始延迟
    this.animateBookCover = true;
    // 动画完成后重置位置到0
    setTimeout(() => {
      this.bookCoverPosition = 0;
    }, 0); // 取消延迟，立即设置位置
    
    // 监听窗口大小变化
    window.addEventListener('resize', this.handleResize);
    
    // 阻止页面默认的拖动行为
    document.addEventListener('dragstart', this.preventDrag);
  },
  beforeUnmount() {
    // 清除事件监听
    window.removeEventListener('resize', this.handleResize);
    document.removeEventListener('dragstart', this.preventDrag);
  },
  methods: {
    preventDrag(e) {
      e.preventDefault();
    },
    handleResize() {
      this.windowWidth = window.innerWidth;
    },
    goBack() {
      // 返回主页面
      if (!this.isTransitioning) {
        this.isTransitioning = true;
        // 使用$root而不是$parent来确保访问到App根组件
        this.$root.switchPage('MainPage');
      }
    },
    startDrag(event) {
      if (this.isTransitioning) return;
      
      this.isDragging = true;
      this.startX = event.clientX;
      this.originalPosition = this.bookCoverPosition;

      // 阻止文本选择
      document.body.style.userSelect = 'none';
      document.body.style.webkitUserSelect = 'none';
    },
    dragBookCover(event) {
      if (!this.isDragging || this.isTransitioning) return;
      
      const deltaX = event.clientX - this.startX;
      let newPosition = this.originalPosition + deltaX;
      
      // 限制向左拖动距离
      if (newPosition < this.maxLeftSlide) {
        newPosition = this.maxLeftSlide;
      }
      
      this.bookCoverPosition = newPosition;
      
      // 检查是否达到返回阈值（右滑超过视口宽度的40%）
      if (newPosition > this.windowWidth * this.transitionThreshold) {
        this.triggerReturnAnimation();
      }
    },
    endDrag() {
      if (!this.isDragging || this.isTransitioning) return;
      
      this.isDragging = false;
      
      // 恢复文本选择
      document.body.style.userSelect = '';
      document.body.style.webkitUserSelect = '';
      
      // 如果没有触发返回，则弹回原位
      if (this.bookCoverPosition < this.windowWidth * this.transitionThreshold) {
        this.resetPosition();
      }
    },
    startTouchDrag(event) {
      if (this.isTransitioning) return;
      
      this.isDragging = true;
      this.startTouchX = event.touches[0].clientX;
      this.originalPosition = this.bookCoverPosition;
    },
    touchDragBookCover(event) {
      if (!this.isDragging || this.isTransitioning) return;
      
      // 阻止页面滚动
      event.preventDefault();
      
      const deltaX = event.touches[0].clientX - this.startTouchX;
      let newPosition = this.originalPosition + deltaX;
      
      // 限制向左拖动距离
      if (newPosition < this.maxLeftSlide) {
        newPosition = this.maxLeftSlide;
      }
      
      this.bookCoverPosition = newPosition;
      
      // 检查是否达到返回阈值
      if (newPosition > this.windowWidth * this.transitionThreshold) {
        console.log('触摸拖动达到返回阈值');
        this.triggerReturnAnimation();
      }
    },
    
    endTouchDrag() {
      if (!this.isDragging || this.isTransitioning) return;
      
      this.isDragging = false;
      
      // 如果没有触发返回，则弹回原位
      if (this.bookCoverPosition < this.windowWidth * this.transitionThreshold) {
        this.resetPosition();
      } else {
        // 如果已经超过阈值但还没有触发动画，则手动触发
        this.triggerReturnAnimation();
      }
    },
    resetPosition() {
      // 动画回到原始位置
      const container = this.$refs.bookCoverContainer;
      if (container) {
        // 回弹动画也使用更慢的速度，与其他动画保持一致
        container.style.transition = 'transform 0.8s cubic-bezier(0.215, 0.61, 0.355, 1)';
        setTimeout(() => {
          this.bookCoverPosition = 0;
          // 恢复拖动状态
          setTimeout(() => {
            container.style.transition = '';
          }, 800); // 与transition-duration一致
        }, 10);
      }
    },
    triggerReturnAnimation() {
      if (this.isTransitioning) return;
      
      this.isTransitioning = true;
      const container = this.$refs.bookCoverContainer;
      console.log('触发返回动画');
      
      if (container) {
        // 添加平滑过渡，动画时间从0.5s增加到1.2s
        container.style.transition = 'transform 1.2s cubic-bezier(0.19, 1, 0.22, 1)';
        
        // 继续向右滑出
        this.bookCoverPosition = this.windowWidth;
        
        // 动画完成后返回主页，等待时间从500ms增加到1200ms
        setTimeout(() => {
          console.log('动画完成，准备返回主页');
          // 直接调用$root.switchPage而不是通过goBack方法
          this.$root.switchPage('MainPage');
        }, 1200);
      }
    }
  }
};
</script>

<style scoped>
.chapter3-page {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #161e22; /* 与MainPage相同的底色 */
  padding: 20px;
  overflow: hidden;
}

.content {
  width: 100%;
  height: 100%;
  border: 3px solid rgba(204, 190, 174, 1); /* 与MainPage相同的边框 */
  border-radius: 16px;
  position: relative;
  overflow: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* IE and Edge */
}

/* 隐藏滚动条 - Webkit浏览器 */
.content::-webkit-scrollbar {
  display: none;
}

/* 书封容器样式 */
.book-cover-container {
  position: absolute;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
  transform: translateX(-100%); /* 初始位置在屏幕左侧外 */
  will-change: transform;
  -webkit-backface-visibility: hidden;
  padding: 0;
  box-sizing: border-box;
  cursor: grab; /* 添加抓取光标 */
  touch-action: pan-y; /* 允许垂直滚动，水平手势会被捕获 */
}

.book-cover-container.animate {
  transition: transform 0.8s cubic-bezier(0.19, 1, 0.22, 1);
  transform: translateX(0); /* 动画后移到中心位置 */
}

.book-cover-container:active {
  cursor: grabbing; /* 抓取时的光标 */
}

.book-cover-svg {
  width: 99%;
  height: auto;
  min-width: calc(100% - 10px);
  user-select: none;
  -webkit-user-drag: none;
  filter: contrast(0.95) brightness(1.05);
}

/* 响应式调整 */
@media (max-width: 768px) {
  .chapter3-page {
    padding: 10px;
  }
  
  .content {
    border-width: 2px;
    border-radius: 12px;
  }
  
  .book-cover-svg {
    width: 99%;
    min-width: calc(100% - 6px); /* 增加最小宽度 */
  }
}

@media (max-width: 576px) {
  .content {
    border-width: 1.5px;
  }
  
  .book-cover-svg {
    width: 99.5%; /* 增加横线宽度 */
    min-width: calc(100% - 4px); /* 增加最小宽度 */
  }
}

@media (max-width: 375px) {
  .content {
    border-width: 1px;
  }
  
  .book-cover-svg {
    width: 100%; /* 最小屏幕下横线占满全宽 */
    min-width: 100%;
  }
}
</style> 