<template>
  <div class="main-page">
    <div class="content">
      <!-- 标题 -->
      <div class="title-container" :class="{ animate: animateTitle }">
        <img src="@/assets/images/标题.svg" alt="标题" class="title-svg" />
      </div>
      
      <!-- 章节列表 -->
      <div class="chapters-container">
        <div
          v-for="(chapter, index) in chapters"
          :key="index"
          class="chapter-item"
          :class="{ animate: animatedChapters >= index + 1 }"
          :style="{ transitionDelay: `${0.1 * index + 0.3}s` }"
          @click="navigateToChapter(chapter.title)"
        >
          <img :src="chapter.svg" :alt="chapter.title" class="chapter-svg" />
        </div>
      </div>

      <!-- 封底 -->
      <div class="footer-container" :class="{ animate: animateFooter }">
        <div class="footer-content">
          <img src="@/assets/images/航标.svg" alt="航标" class="compass-svg" />
          <img src="@/assets/images/封底.svg" alt="封底" class="footer-svg" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'MainPage',
  data() {
    return {
      animateTitle: false,
      animatedChapters: 0,
      animateFooter: false,
      isTransitioning: false, // 添加过渡状态标记
      chapters: [
        { title: '章节1', svg: require('@/assets/images/章节1.svg') },
        { title: '章节2', svg: require('@/assets/images/章节2.svg') },
        { title: '章节3', svg: require('@/assets/images/章节3.svg') },
        { title: '章节4', svg: require('@/assets/images/章节4.svg') },
        { title: '章节5', svg: require('@/assets/images/章节5.svg') },
        { title: '章节6', svg: require('@/assets/images/章节6.svg') },
        { title: '章节7', svg: require('@/assets/images/章节7.svg') },
        { title: '章节8', svg: require('@/assets/images/章节8.svg') },
        { title: '章节9', svg: require('@/assets/images/章节9.svg') },
        { title: '章节10', svg: require('@/assets/images/章节10.svg') },
        { title: '章节11', svg: require('@/assets/images/章节11.svg') },
      ]
    };
  },
  mounted() {
    // 启动标题动画
    setTimeout(() => {
      this.animateTitle = true;
    }, 200);

    // 依次启动章节动画
    let delay = 600; // 标题动画后的延迟
    for (let i = 1; i <= this.chapters.length; i++) {
      setTimeout(() => {
        this.animatedChapters = i;
      }, delay + i * 100); // 每个章节间隔100ms
    }

    // 启动封底动画，在所有章节动画完成后
    setTimeout(() => {
      this.animateFooter = true;
    }, delay + this.chapters.length * 100 + 300);
  },
  methods: {
    navigateToChapter(chapterTitle) {
      // 如果已经在过渡中，则不执行
      if (this.isTransitioning) return;
      
      // 导航到相应章节页面
      if (chapterTitle === '章节3') {
        this.isTransitioning = true;
        
        // 设置动画参数
        const animationDuration = 600; // 从800ms减少到600ms，缩短基础动画时间
        const chapterDelay = 60; // 从80ms减少到60ms，减少章节间的延迟
        const chapterItems = document.querySelectorAll('.chapter-item');
        const chapterItemsArray = Array.from(chapterItems);
        const lastChapterDelay = (chapterItemsArray.length - 1) * chapterDelay;
        
        // 1. 先开始封底和航标下滑动画
        const footerContainer = document.querySelector('.footer-container');
        if (footerContainer) {
          footerContainer.classList.add('exit-down');
          footerContainer.style.transitionDelay = '0ms'; // 立即开始
        }
        
        // 2. 章节依次从下往上左滑出去 - 与封底同时开始
        for (let i = chapterItemsArray.length - 1; i >= 0; i--) {
          // 设置从最后一个章节开始，依次减少延迟
          const delay = (chapterItemsArray.length - 1 - i) * chapterDelay;
          
          chapterItemsArray[i].classList.add('exit-left');
          chapterItemsArray[i].style.transitionDelay = `${delay}ms`;
        }
        
        // 3. 标题延时开始上滑动画，使其与最后一个章节同步结束
        setTimeout(() => {
          const titleContainer = document.querySelector('.title-container');
          if (titleContainer) {
            titleContainer.classList.add('exit-up');
            titleContainer.style.transitionDelay = '0ms';
          }
        }, lastChapterDelay);
        
        // 计算总动画时间并减少额外等待时间
        const totalAnimationTime = animationDuration + lastChapterDelay + 20; // 从50ms减少到20ms
        
        // 4. 等待动画完成后立即切换页面
        setTimeout(() => {
          this.$parent.switchPage('Chapter3Page');
          this.isTransitioning = false;
          
          // 重置动画类，以便返回时可以再次执行动画
          setTimeout(() => {
            const titleContainer = document.querySelector('.title-container');
            if (titleContainer) titleContainer.classList.remove('exit-up');
            if (footerContainer) footerContainer.classList.remove('exit-down');
            chapterItems.forEach(item => {
              item.classList.remove('exit-left');
              item.style.transitionDelay = ''; // 清除transition-delay
            });
          }, 50); // 从100ms减少到50ms
        }, totalAnimationTime);
      }
      // 后续可以添加其他章节的导航
    }
  }
};
</script>

