<template>
  <div id="app">
    <component :is="currentPage" />
    <PromptModal 
      v-model:visible="showPromptModal" 
      :closable="false"
      @confirm="handlePromptConfirm"
      @toggle-music="handleMusicToggle"
    />
  </div>
</template>

<script>
import HomePage from './components/HomePage.vue'
import MainPage from './components/MainPage.vue'
import PromptModal from './components/PromptModal.vue'
import Chapter3Page from './components/Chapter3Page.vue'

// 创建一个简单的事件总线
export const eventBus = {
  events: {},
  emit(event, data) {
    if (this.events[event]) {
      this.events[event].forEach(callback => callback(data));
    }
  },
  on(event, callback) {
    if (!this.events[event]) {
      this.events[event] = [];
    }
    this.events[event].push(callback);
  },
  off(event, callback) {
    if (this.events[event]) {
      this.events[event] = this.events[event].filter(cb => cb !== callback);
    }
  }
};

export default {
  name: 'App',
  components: {
    HomePage,
    MainPage,
    PromptModal,
    Chapter3Page
  },
  data() {
    return {
      currentPage: 'HomePage',
      showPromptModal: true,
      settingsConfig: {
        fullscreen: false,
        music: false
      },
      userInteracted: false // 添加用户交互标志
    }
  },
  mounted() {
    // 检查是否有保存的设置
    this.loadSavedSettings();
    
    // 添加页面可见性变化事件监听
    document.addEventListener('visibilitychange', this.handleVisibilityChange);
  },
  beforeUnmount() {
    // 移除事件监听
    document.removeEventListener('visibilitychange', this.handleVisibilityChange);
  },
  methods: {
    switchPage(page) {
      this.currentPage = page;
    },
    handlePromptConfirm(settings) {
      this.userInteracted = true; // 用户已交互
      this.settingsConfig = settings;
      this.showPromptModal = false;
      
      // 保存设置到本地存储
      this.saveSettings();
      
      // 如果用户选择了全屏模式，在用户交互后请求全屏
      if (this.settingsConfig.fullscreen) {
        this.requestFullscreen();
      }
      
      // 提示框确认后，触发动画开始事件
      eventBus.emit('startHomeAnimation');
    },
    handleMusicToggle(enabled) {
      this.settingsConfig.music = enabled;
      // 后续可以在这里添加音乐控制逻辑
    },
    // 处理页面可见性变化（用于支持移动端刷新）
    handleVisibilityChange() {
      if (document.visibilityState === 'visible') {
        // 页面变为可见时，检查是否需要恢复设置
        this.loadSavedSettings();
      }
    },
    // 保存设置到本地存储
    saveSettings() {
      localStorage.setItem('appSettings', JSON.stringify({
        fullscreen: this.settingsConfig.fullscreen,
        music: this.settingsConfig.music,
        welcomed: true // 标记用户已经通过欢迎页面
      }));
    },
    // 从本地存储加载设置
    loadSavedSettings() {
      try {
        const savedSettings = localStorage.getItem('appSettings');
        if (savedSettings) {
          const settings = JSON.parse(savedSettings);
          
          // 恢复保存的设置
          this.settingsConfig = {
            fullscreen: settings.fullscreen || false,
            music: settings.music || false
          };
          
          // 如果用户已经通过欢迎页面，不再显示提示框
          if (settings.welcomed) {
            this.showPromptModal = false;
            // 直接开始动画
            setTimeout(() => {
              eventBus.emit('startHomeAnimation');
            }, 100);
          }
          
          // 不要在这里自动请求全屏，因为没有用户交互
          // 只记录设置，但不自动应用全屏
        }
      } catch (e) {
        console.error('加载设置时出错:', e);
      }
    },
    // 检查当前是否已全屏
    isCurrentlyFullscreen() {
      return !!(document.fullscreenElement || document.mozFullScreenElement || 
                document.webkitFullscreenElement || document.msFullscreenElement);
    },
    // 请求全屏 - 只在用户交互后调用
    requestFullscreen() {
      // 确保只在用户交互后请求全屏
      if (!this.userInteracted) return;
      
      try {
        const docElm = document.documentElement;
        if (docElm.requestFullscreen) {
          docElm.requestFullscreen();
        } else if (docElm.mozRequestFullScreen) { // Firefox
          docElm.mozRequestFullScreen();
        } else if (docElm.webkitRequestFullscreen) { // Chrome, Safari
          docElm.webkitRequestFullscreen();
        } else if (docElm.msRequestFullscreen) { // IE11
          docElm.msRequestFullscreen();
        }
      } catch (error) {
        console.warn('请求全屏失败:', error);
      }
    }
  }
}
</script>

<style>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: fixed; /* 防止移动端弹性滚动 */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

#app {
  width: 100%;
  height: 100vh;
  background-color: #000;
  color: #fff;
  position: relative;
  overscroll-behavior: none; /* 阻止下拉刷新手势 */
  touch-action: manipulation; /* 提高触摸响应速度 */
}

/* 处理iOS的刷新手势 */
@supports (-webkit-touch-callout: none) {
  body {
    /* iOS特定的样式 */
    -webkit-overflow-scrolling: touch;
    overscroll-behavior-y: none;
  }
}
</style>
